.incart-item {
  width: 100%;
  border: 1px solid rgb(234, 233, 233);
  display: grid;
  grid-template-columns: 3fr 7fr;
  gap: 30px;
}

.incart-item>div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.incart-item img {
  width: 60%;
  align-self: center;
}

.incart-item>div:nth-child(1) a {
  /* border: 1px solid red; */
  display: flex;
  justify-content: center;
}

.incart-item .item-details {
  justify-content: baseline;
}

.item-details h2 {
  font-weight: 900;
  font-size: 20px;
  text-align: left;
}

.item-details p {
  font-size: 18px;
  text-align: left;
}

.item-details>div {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
  font-size: 18px;
}

.item-details>div button {
  border: none;
  background-color: rgb(239, 235, 235);
  width: 35px;
  height: 35px;
  text-align: center;
  border-radius: 50%;
  font-size: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.item-details>div button:hover {
  background-color: lightgray;
}

.item-details>div button span {
  text-align: center;
}